import {Grid, List, ListItem, ListItemText, Paper} from "@mui/material";
import BurnSupply from "../components/BurntSupply";
import Web3 from "web3";
import coineus_abi from "../abis/coineus.json";
import {makeBatchRequest} from "../utils/promisify";
import {useEffect, useState} from "react";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import {useSelector} from "react-redux";

const web3 = new Web3("https://rpc.fuse.io");
const ceus_contract = new web3.eth.Contract(coineus_abi, "0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8");

const calls = [
    ceus_contract.methods.totalSupply().call,
    ceus_contract.methods.balanceOf('0x3b46eFdd411cc0Db43f1BE7853727e18Cce8b85b').call,
    ceus_contract.methods.balanceOf('0xb80267eA7fa368374ee4d4Bf10044778232AdEFe').call,
    ceus_contract.methods.balanceOf('0x87440f829e7ed808599749630555080917c4b3d9').call,
    ceus_contract.methods.balanceOf('0xE27431FACb1F093BA164c9F86B3f484b39ad54CC').call,
    ceus_contract.methods.balanceOf('0x1306d7342edfe9c3686ad044e47d8b8fa2d6a7f0').call,
    ceus_contract.methods.balanceOf('0x0A00b9f2A2BB635f532616f18b1D50f8Bd0276dA').call,
    ceus_contract.methods.balanceOf('0xcEAB8d0303924D3DB5B3a3eD178F055603398845').call,

];

export default function Stats() {

    const {wallet} = useSelector(state => state.coineus);

    const [supply, setSupply] = useState(0);
    const [liquidity, setLiquidity] = useState(0);
    const [fund, setFund] = useState(0);
    const [litLP, setLitLP] = useState(0);
    const [ceusoneContract, setCeusoneContract] = useState(0);
    const [bbksLP, setBbksLP] = useState(0);
    const [ceusETH, setCeusETH] = useState(0);
    const [ladyLP, setLadyLP] = useState(0);

    useEffect(() => {
        makeBatchRequest(web3, calls).then(resp => {
            let [
                ceus_supply,
                ceus_liquidity,
                ceus_fund,
                lit_lp,
                ceusone,
                bbks,
                ceusOnETH,
                lady_lp
            ] = resp;

            setSupply(ceus_supply / (10 ** 18));
            setLiquidity(ceus_liquidity / (10 ** 18));
            setFund(ceus_fund / (10 ** 18));
            setLitLP(lit_lp / (10 ** 18));
            setCeusoneContract(ceusone / (10 ** 18));
            setBbksLP(bbks / (10 ** 18));
            setCeusETH(ceusOnETH / (10 ** 18));
            setLadyLP(lady_lp / (10 **18))
        })
    }, [])

    return (<Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
            <Grid item xs={12}>
                <BurnSupply
                    minted={2000000}
                    supply={supply}
                    liquidity={liquidity}
                    fund={fund}
                    litLP={litLP}
                    ceusone={ceusoneContract}
                    bbksLP={bbksLP}
                    ceusOnETH={ceusETH}
                    ladyLP={ladyLP}
                />
            </Grid>
            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemText
                                primary="Coineus on FUSE"
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Voltage Finance"
                            />
                            <ListItemText
                                primary={CoineusUSDFormat(wallet.prices.fuse?.["0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8"]?.inUSD)}
                                secondary={CoineusCryptoFormat(wallet.prices.fuse?.["0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8"]?.inFuse) + " FUSE"}
                                primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemText
                                primary="Coineus on ETH"
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary="Uniswap V3"
                            />
                            <ListItemText
                                primary={CoineusUSDFormat(wallet.prices.eth?.["0x496443ea0c46Ea3aF7025F071EA3FBd5F1f1D560"]?.inUSD)}
                                secondary={CoineusCryptoFormat(wallet.prices.eth?.["0x496443ea0c46Ea3aF7025F071EA3FBd5F1f1D560"]?.inETH) + " ETH"}
                                primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                    </List>
                </Paper>
            </Grid>
        </Grid>
    )

}